import { LinksContext } from "context/LinksProvider"
import { useContext, useEffect, useRef, useState } from "react"
import Container from "./Container"

const Header = () => {
  const headerRef = useRef(null)
  const linksContext = useContext(LinksContext)

  console.log(linksContext)

  useEffect(() => {
    window.addEventListener('scroll', () => {
      if (window.pageYOffset === 0) {
        headerRef.current.classList.remove('header--with-shadow')
      } else {
        headerRef.current.classList.add('header--with-shadow')
      }
    })
  }, [])

  return (
    <header className={'header'} ref={headerRef}>
      <Container rootClasses={'header__container'}>

        <div className={'header__body'}>
          <div className={'header__logo'}>
            Daler
          </div>
          <div className={'header__nav'}>
            <ul className={'header__nav-list'}>
              {linksContext.links.map((link, i) => (
                <li
                  key={i}
                  className={`header__nav-link ${linksContext.selected === i && 'header__nav-link--active'}`}
                >
                  <a href={link.to} onClick={() => {
                    linksContext.setSelected(i)
                  }}>
                    {link.text}
                  </a>
                </li>
              ))}
            </ul>
          </div>
        </div>

      </Container>  
    </header>
  )
}

export default Header
