import ArrowIcon from '@material-ui/icons/ExpandLess'
import { useRef } from 'react'
import { useEffect } from 'react'

const Up = (props) => {
  const upRef = useRef(null)

  useEffect(() => {
    window.addEventListener('scroll', () => {
      if (window.pageYOffset > 20) {
        upRef.current.classList.add('up--visible')
      } else {
        upRef.current.classList.remove('up--visible')
      }
    })
  }, [])

  const handleClicked = () => {
    window.scrollTo(0, 0)
  }

  return (
    <div className={'up'} ref={upRef} onClick={handleClicked}>
      <ArrowIcon className={'up__icon'} />
    </div>
  )
}

export default Up
