import pt from 'prop-types'
import Button from './Button'

const Project = (props) => {
  return (
    <div className={'project'}>
      <div className={'project__left'}>
        <div className={'project__img-container'}>
          <img src={props.imgRef} alt={'Wait'} className={'project__img'} />
        </div>
      </div>
      <div className={'project__right'}>
        <div className={'project__title'}>
          {props.title}
        </div>
        <p className={'project__desc'}>
          {props.desc}
        </p>
        <div className={'project__btns'}>
          <a href={props.demoUrl} target={'_blank'}>
            <Button
              color={'primary'}
              children={'Demo'}
              Icon={'uil uil-arrow-right'}
              rootClasses={'project__btn'}
            />
          </a>
          <a href={props.codeUrl} target={'_blank'}>
            <Button
              color={'primary'}
              children={'Code'}
              Icon={'uil uil-arrow-right'}
              rootClasses={'project__btn'}
            />
          </a>
        </div>
      </div>
    </div>
  )
}

Project.propTypes = {
  imgRef: pt.string.isRequired,
  title: pt.string.isRequired,
  desc: pt.string.isRequired,
  demoUrl: pt.string.isRequired,
  codeUrl: pt.string.isRequired
}

export default Project
