import SectionTitle from "./SectionTitle"
import SkillsCategory from "./SkillsCategory"

const Skills = () => {
  return (
    <section className={'section skills'} id={'skills'}>

      <SectionTitle lg={'Skills'} sm={'My tecknical knowledge'} />

      <div className={'skills__body'}>
        <SkillsCategory
          title={'Frontend developer'}
          subTitle={'More than 1,5+ years'}
          iconClassName={'uil uil-brackets-curly'}
          skills={[
            { name: 'HTML', percent: '90%' },
            { name: 'CSS', percent: '90%' },
            { name: 'JavaScript', percent: '90%' },
            { name: 'React', percent: '90%' },
            { name: 'Vue', percent: '60%' },
          ]}
        />
        <SkillsCategory
          title={'Design'}
          subTitle={'More than 6 mongths'}
          iconClassName={'uil uil-ruler-combined'}
          skills={[
            { name: 'Figma', percent: '70%' },
            { name: 'Adobe Photoshop', percent: '30%' },
          ]}
        />
        <SkillsCategory
          title={'Backend developer'}
          subTitle={'More than 1+ years'}
          iconClassName={'uil uil-notebooks'}
          skills={[
            { name: 'NodeJs', percent: '50%' },
            { name: 'MongoDB', percent: '50%' },
            { name: 'Django', percent: '60%' },
          ]}
        />
      </div>

    </section>
  )
}

export default Skills
