const Footer = () => {
  return (
    <footer className={'footer'}>
      <div className={'container footer__container'}>
        <div className={'footer__body'}>

          <div className={'footer__left'}>
            <div className={'footer__logo-lg'}>Daler</div>
            <div className={'footer__logo-sm'}>Frontend developer</div>
          </div>
          <div className={'footer__center'}>
            <ul className={'footer__links'}>
              <li className={'footer__link'}>
                <a href={'#home'}>Home</a>
              </li>
              <li className={'footer__link'}>
                <a href={'#projects'}>Projects</a>
              </li>
              <li className={'footer__link'}>
                <a href={'#about'}>About</a>
              </li>
            </ul>
          </div>
          <div className={'footer__right'}>
            <ul className={'footer__social-links'}>
              <li className={'footer__social-link'}>
                <a href={'https://github.com/dalerfrontend/'}>
                  <i className={'uil uil-github-alt footer__social-icon'}></i>
                </a>
              </li>
              <li className={'footer__social-link'}>
                <a href={'http://www.instagram.com/dalerfrontend/?hl=ru'}>
                  <i className={'uil uil-instagram footer__social-icon'}></i>
                </a>
              </li>
              <li className={'footer__social-link'}>
                <a href={'https://twitter.com/DalerSaidov2003'}>
                  <i className={'uil uil-twitter-alt footer__social-icon'}></i>
                </a>
              </li>
            </ul>
          </div>

          <div className={'footer__rights'}>
            All rights reserved 2021
          </div>

        </div>
      </div>
    </footer>
  )
}

export default Footer
