import CloseIcon from '@material-ui/icons/CloseOutlined'
import { LinksContext } from "context/LinksProvider"
import { useContext, useEffect, useState, useRef } from "react"

const BottomMenu = () => {
  const [linksPanelOpened, setLinksPanelOpened] = useState(false)
  const linksContext = useContext(LinksContext)
  const shadeRef = useRef(null)

  const handleOpenLinks = () => setLinksPanelOpened(true)
  const handleCloseLinks = () => setLinksPanelOpened(false)
  const handleChangeTheme = () => console.log('Feature is not implemented')

  useEffect(() => {
    if (linksPanelOpened) {
      shadeRef.current.classList.add('shade--visible')
      document.querySelector('body').classList.add('lock')
    } else {
      shadeRef.current.classList.remove('shade--visible')
      document.querySelector('body').classList.remove('lock')
    }
  }, [linksPanelOpened])

  return <>
    <div className={'bottom-menu'}>

      <div className={'container bottom-menu__container'}>
        <div className={'bottom-menu__body'}>

          <div className={'bottom-menu__logo'}>
            Daler
          </div>

          <div className={`bottom-menu__right`}>
            {/* <button className={'bottom-menu__btn'} onClick={handleChangeTheme}>
              <i className={'uil uil-moon bottom-menu__icon'}></i>
            </button> */}
            <button className={'bottom-menu__btn'} onClick={handleOpenLinks}>
              <i className={'uil uil-apps bottom-menu__icon'}></i>
            </button>
          </div>

        </div>
      </div>

      <div
        id={'links-panel'}
        className={`bottom-menu__links-panel ${linksPanelOpened && 'bottom-menu__links-panel--opened'}`}
      >
        
        <ul className={'bottom-menu__links'}>
          {linksContext.links.map((link, i) => (
            <li
              key={i}
            >
              <a
                href={link.to} className={`bottom-menu__link ${linksContext.selected === i && 'bottom-menu__link--active'}`}
                onClick={() => {
                  linksContext.setSelected(i)
                  handleCloseLinks()
                }}
              >
                <i className={`bottom-menu__link-icon ${link.icon}`}></i>
                <div className={'bottom-menu__link-text'}>
                  {link.text}
                </div>
              </a>
            </li>
          ))}
        </ul>

        <button
          type={'button'}
          className={'bottom-menu__close-links-btn'}
          onClick={handleCloseLinks}
        >
          <CloseIcon />
        </button>

      </div> 

    </div>
    <div
      className={'shade'}
      ref={shadeRef}
      onClick={handleCloseLinks}
    ></div>
  </>
}

export default BottomMenu
