import { createContext, useState } from "react"

const LinksContext = createContext(null)

const LinksProvider = (props) => {
  const [selected, setSelected] = useState(0)

  const links = [
    { to: '#home', text: 'Home', icon: 'uil uil-estate' },
    { to: '#about', text: 'About', icon: 'uil uil-user-circle' },
    { to: '#skills', text: 'Skills', icon: 'uil uil-book-open' },
    { to: '#projects', text: 'Projects', icon: 'uil uil-image' },
    { to: '#contact', text: 'Contact', icon: 'uil uil-message' },
  ]

  return (
    <LinksContext.Provider value={{ links, selected, setSelected }}>
      {props.children}
    </LinksContext.Provider>
  )
}

export { LinksContext }

export default LinksProvider
