import Button from "./Button"
import cv from 'assets/Daler-CV.pdf'
import Tilt from 'react-tilt'
import SectionTitle from "./SectionTitle"
import aboutImg from 'assets/images/about.jpg'
import Section from "./Section"


const About = () => {
  return (
    <Section rootClasses={'about'} id={'about'}>
      
      <SectionTitle lg={'About'} sm={'My introduction'} />

      <div className={'about__body'}>
        <div className={'about__img-container'}>
          <Tilt options={{ max: 20, scale: 1 }}>
            <img
              className={'about__img'}
              src={aboutImg}
              alt={'Wait'}
            />
          </Tilt>
        </div>
        <div className={'about__main'}>
          <p className={'about__text'}>
            Outstanding, experienced frontend developer.
            I describe myself as a responsible, passionate
            software engineer. I am a good team worker and
            problem solver. With this said, I can bring
            your company to success.
          </p>
          <ul className={'about__info-items'}>
            <li className={'about__info-item-wrapper'}>
              <div className={'about__info-item'}>
                <div className={'about__info-item-num'}>
                  1.5+
                </div>
                <div className={'about__info-item-desc'}>
                  {`Years of
                  experiece`}
                </div>
              </div>
            </li>
            <li className={'about__info-item-wrapper'}>
              <div className={'about__info-item'}>
                <div className={'about__info-item-num'}>
                  10+
                </div>
                <div className={'about__info-item-desc'}>
                  {`Real life
                    projects`}
                </div>
              </div>
            </li>
            <li className={'about__info-item-wrapper'}>
              <div className={'about__info-item'}>
                <div className={'about__info-item-num'}>
                  10+
                </div>
                <div className={'about__info-item-desc'}>
                  {`Happy
                    clients`}
                </div>
              </div>
            </li>
          </ul>
          <a href={cv} download={'Daler-CV.pdf'}>
            <Button
              color={'primary'}
              Icon={'uil uil-import'}
              children={'Download CV'}
            />
          </a>
        </div>
      </div>

    </Section>
  )
}

export default About
