import Button from "./Button"
import Section from "./Section"
import avatarImg from 'assets/images/avatar.png'

const Home = () => {
  return (
    <Section rootClasses={'home'} id={'home'}>

      <div className={'home__body'}>

        <div className={'home__social-section'}>
          <ul className={'home__social-links'}>
            <li className={'home__social-link'}>
              <a href={'https://github.com/dalerfrontend/'}>
                <i className={'uil uil-github-alt home__social-icon'}></i>
              </a>
            </li>
            <li className={'home__social-link'}>
              <a href={'http://www.instagram.com/dalerfrontend/?hl=ru'}>
                <i className={'uil uil-instagram home__social-icon'}></i>
              </a>
            </li>
            <li className={'home__social-link'}>
              <a href={'https://twitter.com/DalerSaidov2003'}>
                <i className={'uil uil-twitter-alt home__social-icon'}></i>
              </a>
            </li>
          </ul>
        </div>

        <div className={'home__main-section'}>
          <h1 className={'home__title'}>
            Hi, I'm Daler
          </h1>
          <h2 className={'h3 home__title-sm'}>
            Frontend developer
          </h2>
          <p className={'home__about'}>
            {`High level experience in web development
              and design, producing products with high
              quality`}
          </p>
          <a href={'#contact'}>
            <Button
              color={'primary'}
              Icon={'uil uil-message'}
              children={'Contact Me'}
            />
          </a>
          <a href={'#about'} className={'home__scroll-down'}>
            <i className={'uil uil-mouse-alt home__mouse-icon'}></i>
            <span className={'home__scroll-down-text'}>
              Scroll down
            </span>
            <i className={'uil uil-arrow-down home__arrow-down-icon'}></i>
          </a>
        </div>

        <div className={'home__avatar-section'}>
          <ul className={'home__social-links'}>
            <li className={'home__social-link'}>
              <i className={'uil uil-github-alt home__social-icon'}></i>
            </li>
            <li className={'home__social-link'}>
              <i className={'uil uil-instagram home__social-icon'}></i>
            </li>
            <li className={'home__social-link'}>
              <i className={'uil uil-twitter-alt home__social-icon'}></i>
            </li>
          </ul>
          <img
            className={'home__avatar'}
            src={avatarImg}
            // src={'https://picsum.photos/id/1/500/500'}
            alt={'Wait'}
          />
        </div>

      </div>
      
    </Section>
  )
}

export default Home
