import { Swiper, SwiperSlide } from 'swiper/react'
import SwiperCore, { Navigation, Pagination, Scrollbar, A11y } from 'swiper'

import 'swiper/swiper.scss'
import 'swiper/swiper.scss'
import 'swiper/components/navigation/navigation.scss'
import 'swiper/components/pagination/pagination.scss'
import 'swiper/components/scrollbar/scrollbar.scss'

import Project from './Project'

import appleProjectImg from 'assets/images/projects/apple-store.png'
import todoProjectImg from 'assets/images/projects/todo.jpg'
import socialMediaProjectImg from 'assets/images/projects/social-media.jpg'
import designLabProjectImg from 'assets/images/projects/design-lab.png'
import funiroProjectImg from 'assets/images/projects/funiro.jpg'

import SectionTitle from './SectionTitle'
import Section from './Section'



SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

const Projects = () => {
  return (
    <Section rootClasses={'projects'} id={'projects'}>

      <SectionTitle lg={'Projects'} sm={'My pet-projects'} />

      <div className={'projects__body'}>
        <Swiper
          spaceBetween={0}
          slidesPerView={1}
          navigation
          pagination={{ clickable: true }}
          className={'projects__swiper'}
        >
          <SwiperSlide className={'projects__slide'}>
            <Project
              imgRef={appleProjectImg}
              title={'Apple Store'}
              desc={'Fully responsive website with brilliant color pallete!'}
              demoUrl={'https://dalerfrontend.github.io/apple-store/'}
              codeUrl={'https://github.com/dalerfrontend/apple-store'}
            />
          </SwiperSlide>
          <SwiperSlide className={'projects__slide'}>
            <Project
              imgRef={todoProjectImg}
              title={'Todo List'}
              desc={'React made web app you can use to increase your productivity'}
              demoUrl={'https://dalerfrontend.github.io/todo/'}
              codeUrl={'https://github.com/dalerfrontend/todo-react'}
            />
          </SwiperSlide>
          <SwiperSlide className={'projects__slide'}>
            <Project
              imgRef={designLabProjectImg}
              title={'Design Lab'}
              desc={'Another beautiful responsibe website with a lot of features'}
              demoUrl={'https://dalerfrontend.github.io/design-lab/'}
              codeUrl={'https://github.com/dalerfrontend/design-lab'}
            />
          </SwiperSlide>
          <SwiperSlide className={'projects__slide'}>
            <Project
              imgRef={socialMediaProjectImg}
              title={'Social Media'}
              desc={'Amazing social media, made using React, Redux and Material UI'}
              demoUrl={'https://dalerfrontend.github.io/social-media/'}
              codeUrl={'https://github.com/dalerfrontend/social-media-react/'}
            />
          </SwiperSlide>
        </Swiper>
      </div>

    </Section>
  )
}

export default Projects
