import pt from 'prop-types'

const SkillsCategory = (props) => {
  return (
    <div className={'skills-category'}>

      <div className={'skills-category__body'}>
        <div className={'skills-category__body-left'}>
          <i className={`${props.iconClassName} skills-category__icon`}></i>
        </div>
        <div className={'skills-category__body-center'}>
          <h4 className={'skills-category__title-lg'}>{props.title}</h4>
          <div className={'skills-category__title-sm'}>{props.subTitle}</div>
        </div>
        <div className={'skills-category__body-right'}>
          <i className={`uil uil-angle-down skills-category__arrow-down-icon`}></i>
        </div>
      </div>

      <div className={'skills-category__popup'}>
        {props.skills.map((skill, i) => (
          <li className={'skills-category__skill'} key={i}>
            <div className={'skills-category__skill-header'}>
              <span className={'skills-category__skill-name'}>{skill.name}</span>
              <div className={'skills-category__skill-percent'}>{skill.percent}</div>
            </div>
            <div className={'skills-category__progress'}>
              <div style={{ width: skill.percent}} className={'skills-category__inner'}>

              </div>
            </div>
          </li>
        ))}
      </div>

    </div>
  )
}

SkillsCategory.propTypes = {
  title: pt.string.isRequired,
  subTitle: pt.string.isRequired,
  iconClassName: pt.string.isRequired,
  skills: pt.array.isRequired,
}

export default SkillsCategory
