import pt from 'prop-types'

const Button = (props) => {
  return (
    <button
      className={`button button--color-${props.color} ${props.rootClasses && props.rootClasses}`}
      onClick={props.onClick}
    >
      <span className={'button__text'}>
        {props.children}
      </span>
      <i className={`button__icon ${props.Icon}`}></i>
    </button>
  )
}

Button.propTypes = {
  color: pt.string.isRequired,
  Icon: pt.any.isRequired,
  children: pt.string.isRequired,
  rootClasses: pt.string,
  onClick: pt.func
}

export default Button
