import { useFormik } from "formik"
import Button from "./Button"
import { firestore } from 'firebaseApp'
import { useState } from "react"
import SectionTitle from "./SectionTitle"
import Section from "./Section"

const Contact = () => {
  const [message, setMessage] = useState('')
  const form = useFormik({
    initialValues: {
      name: '',
      email: '',
      phone: '',
      message: ''
    },
    validate(v) {
      const errors = {}
      if (!v.name.trim()) {
        errors.emptyName = true
      }
      if (!v.email.trim()) {
        errors.emptyEmail = true
      }
      if (!v.phone.trim()) {
        errors.emptyPhone = true
      }
      if (!v.message.trim()) {
        errors.emptyMessage = true
      }
      return errors
    },
    onSubmit(v) {
      firestore.collection('resume-messages').add(v)
      form.resetForm()
      setMessage('Message sent successfully!')
    }
  })

  return (
    <Section rootClasses={'contact'} id={'contact'}>

      <SectionTitle lg={'Contact'} sm={'Get in touch'} />

      <div className={'contact__body'}>

        <div className={'contact__left'}>
          <ul className={'contact__contact-methods'}>

            <li className={'contact__method'}>
              <i className={`uil uil-phone contact__method-icon`}></i>
              <div className={'contact__method-main'}>
                <div className={'contact__method-text-lg'}>
                  Call Me
                </div>
                <div className={'contact__method-text-sm'}>
                  +998 91 400-74-39
                </div>
              </div>
            </li>

            <li className={'contact__method'}>
              <i className={`uil uil-envelope contact__method-icon`}></i>
              <div className={'contact__method-main'}>
                <div className={'contact__method-text-lg'}>
                  Email
                </div>
                <div className={'contact__method-text-sm'}>
                  daler.developer@mail.ru
                </div>
              </div>
            </li>

            <li className={'contact__method'}>
              <i className={`uil uil-location-point contact__method-icon`}></i>
              <div className={'contact__method-main'}>
                <div className={'contact__method-text-lg'}>
                  Location
                </div>
                <div className={'contact__method-text-sm'}>
                  Tashkent, Chilanzar
                </div>
              </div>
            </li>

          </ul>
        </div>

        <div className={'contact__right'}>
          <form className={'contact__form'}>
            <input
              name={'name'}
              className={`contact__input contact__input-name ${form.touched.name && form.errors.emptyName && 'contact__input--empty'}`}
              placeholder={'Name'}
              value={form.values.name}
              onChange={form.handleChange}
              onBlur={form.handleBlur}
            />
            <input
              name={'email'}
              className={`contact__input contact__input-email ${form.touched.email && form.errors.emptyEmail && 'contact__input--empty'}`}
              placeholder={'Email'}
              value={form.values.email}
              onChange={form.handleChange}
              onBlur={form.handleBlur}
            />
            <input
              name={'phone'}
              className={`contact__input contact__input-phone ${form.touched.phone && form.errors.emptyPhone && 'contact__input--empty'}`}
              placeholder={'Phone'}
              value={form.values.phone}
              onChange={form.handleChange}
              onBlur={form.handleBlur}
            />
            <textarea
              name={'message'}
              className={`contact__input contact__input-message ${form.touched.message && form.errors.emptyMessage && 'contact__input--empty'}`}
              placeholder={'Message'}
              value={form.values.message}
              onChange={form.handleChange}
              onBlur={form.handleBlur}
            >
            </textarea>
          </form>
          <Button
            color={'primary'}
            children={'Send Message'}
            Icon={'uil uil-message'}
            rootClasses={'contact__send-btn'}
            onClick={form.handleSubmit}
          />
          <span className={'contact__message'}>
            {message}
          </span>
        </div>

      </div>

    </Section>
  )
}

export default Contact
